<template>
<div>
    <div style="border-bottom: 1px solid #e9e9e9;padding-bottom:6px;margin-bottom:6px;">
        <Checkbox
            :indeterminate="indeterminate"
            :value="checkAll"
            @click.prevent.native="handleCheckAll">全选</Checkbox>
    </div>
    <CheckboxGroup v-model="checkAllGroup" @on-change="checkAllGroupChange">
        <template v-for="food in foodList"><Checkbox :label="food.foodId" :key="food.foodId" :value='foodSelect'>{{food.foodName}}</Checkbox></template>
    </CheckboxGroup>
    <Button type="error" @click="delButton" class='delButton'>删除食品</Button>
    </div>
</template>
<script>
import {selectManyFood,batchDelFood} from '@/api/api.js'
export default {
    data(){
        return{
            indeterminate: true,
                checkAll: false,
                checkAllGroup: [],
                canteenId:this.$route.query.canteenId,
                foodList:[],
                foodSelect:false,
        }
    },
    mounted(){
        let data={canteenId:this.canteenId}
        selectManyFood(data).then(res=>{
            if(res.data.status==200){
                this.foodList=res.data.data
            }           
        },err=>{
            this.$Message.error('网络错误');
        })
    },
    methods:{
        delButton(){
            if(this.checkAllGroup.length==0){
                this.$Message.error('请先选择要删除的食品')
                return
            }        
            let data={foods:this.checkAllGroup.toString()}
            batchDelFood(data).then(res=>{
                if(res.data.status==200){
                    this.$Message.success('删除成功')
                    this.$router.push({name:'sellerMain'})
                }else{
                    this.$Message.error('删除失败')
                }
            },err=>{
                this.$Message.error('网络出错')
            })
        },
          handleCheckAll () {
                if (this.indeterminate) {
                    this.checkAll = false;
                } else {
                    this.checkAll = !this.checkAll;
                }
                this.indeterminate = false;
                if (this.checkAll) {
                    this.checkAllGroup = this.foodList
                    this.foodSelect=true
                } else {
                    this.checkAllGroup = []
                    this.foodSelect=false
                }
            },
            checkAllGroupChange (data) {
                if (data.length === this.foodList.length) {
                    this.indeterminate = false;
                    this.checkAll = true;
                } else if (data.length > 0) {
                    this.indeterminate = true;
                    this.checkAll = false;
                } else {
                    this.indeterminate = false;
                    this.checkAll = false;
                }
            }
    }
}
</script>
<style lang="less">
.delButton{
    margin-top: 20px
}
</style>

